<template>
    <div id="advance-table">
        
        <!-- 表格搜索 start -->
        <div class="search-content clearfix">

                <div class="search-item left clearfix">
                    <div class="search-label left">预定时间</div>
                    <div  class="search-detail left">
                        <el-date-picker
                            @change="beginTimeChange"
                            v-model="beginTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </div>
                    <div  class="search-detail time left">
                        <el-date-picker
                            @change="endTimeChange"
                            v-model="endTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </div>
                </div>

                <div class="search-item left clearfix">
                    <div class="search-label left">状态</div>
                    <div class="search-detail left">
                        <el-select v-model="reservateStatusValue" placeholder="请选择">
                            <el-option
                            v-for="item in reservateStatusOption"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>

                <div class="search-item left clearfix input-text">
                    <el-input v-model="customerName" placeholder="预订人姓名"></el-input>
                </div>
                <div class="search-item left clearfix input-sec input-text">
                    <el-input @keyup.native="checkPhoneNum" v-model="customerPhone" placeholder="预订人电话"></el-input>
                </div>
                <div class="search-item left clearfix input-sec input-text">
                    <el-input v-model="employeeName" placeholder="接收人姓名"></el-input>
                </div>

                <div class="search-item right clearfix seat">
                    <el-button class="searchBtn right" @click="getList">搜索</el-button>
                </div>
        </div>
        <!-- 表格搜索 end -->

        <!-- table start -->
        <div class="table-content">
            <el-table :data="list" stripe height="660" @sort-change="sort">
                <el-table-column min-width="80" align="center" label="预定时间" sortable="true" prop="sort_reservation_time">
                    <template slot-scope="scope">
                        <span>{{ scope.row.reservation_time}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="预订人">
                    <template slot-scope="scope">
                        <span>{{ scope.row.customer_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="联系电话">
                    <template slot-scope="scope">
                        <span>{{ scope.row.customer_phone}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="用餐人数" sortable="true" prop="sort_customer_num">
                    <template slot-scope="scope">
                        <span>{{ scope.row.customer_num}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="餐桌类型">
                    <template slot-scope="scope">
                        <span>{{ scope.row.seat_type}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="餐桌名称">
                    <template slot-scope="scope">
                        <span>{{ scope.row.seat_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="状态">
                    <template slot-scope="scope">
                        <span>{{ ReservationStatus.toString(scope.row.reservation_status)}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="备注">
                    <template slot-scope="scope">
                        <span>{{ scope.row.note}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="创建人">
                    <template slot-scope="scope">
                        <span>{{ scope.row.employee_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="签到时间" sortable="true" prop="sort_sign_time">
                    <template slot-scope="scope">
                        <span>{{ scope.row.sign_time }}</span>
                    </template>
                </el-table-column>                                                                                                                                                
            </el-table>

            <empty-table v-if="isListEmpty"></empty-table>
        </div>
        <!-- table end -->

        <!-- 分页 starat -->
        <div class="pagination-container">
            <el-pagination 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page.sync="listQuery.page" 
            :page-sizes="[10,20,40]" 
            :page-size="listQuery.limit" 
            layout="sizes, jumper, prev, pager, next" 
            :total="total">
            </el-pagination>
        </div>
        <!-- 分页 end -->
    </div>
</template>

<script>
import moment from "moment";
import EmptyTable from "@/components/emptytable/main"; //空列表

import { ls } from "@/config/pageStore";
import { advanceList } from "../api";
import { ReservationStatus } from "@/config/cfg";

export default {
  components: {
    EmptyTable
  },
  data() {
    return {
      reservateStatusOption: [
        {
          value: 0,
          label: "全部"
        },
        {
          value: ReservationStatus.NOSIGN,
          label: ReservationStatus.toString(ReservationStatus.NOSIGN)
        },
        {
          value: ReservationStatus.SIGN,
          label: ReservationStatus.toString(ReservationStatus.SIGN)
        },
        {
          value: ReservationStatus.CANCEL,
          label: ReservationStatus.toString(ReservationStatus.CANCEL)
        }
      ],

      list: null,
      total: null, //分页总条数
      listQuery: {
        page: 1,
        limit: 10,
        importance: undefined,
        title: undefined,
        type: undefined,
        sort: "+id"
      },

      //搜索
      beginTime: "",
      endTime: "",
      reservateStatusValue: "",
      customerName: "",
      customerPhone: "",
      employeeName: "",

      ReservationStatus: ReservationStatus,

      //排序
      isSort: false,
      sortLabel: "",
      sortOrder: 1,

      isListEmpty: true
    };
  },
  created() {
    this.listQuery.limit = ls.getItem("SLSTATISTICALLFORMSIZE") || 10;
    this.getList();
  },
  methods: {
    getList() {
      let data = {
        get_reservation_all: 1,
        page_size: this.listQuery.limit,
        page_no: this.listQuery.page
      };

      //预定时间开始
      if (this.beginTime !== "") {
        data["begin_time"] = parseInt(moment(this.beginTime).format("X"));
      }

      //预定时间结束
      if (this.endTime !== "") {
        data["end_time"] = parseInt(moment(this.endTime).format("X"));
      }

      //预定状态
      if (this.reservateStatusValue !== "" && this.reservateStatusValue !== 0) {
        data["reservation_status"] = this.reservateStatusValue;
      }

      //预定人姓名
      if (this.customerName !== "") {
        data["customer_name"] = this.customerName;
      }

      //预定人电话
      if (this.customerPhone !== "") {
        data["customer_phone"] = this.customerPhone;
      }

      //接收人姓名
      if (this.employeeName !== "") {
        data["employee_name"] = this.employeeName;
      }

      //是否有排序
      if (this.isSort) {
        data[this.sortLabel] = this.sortOrder;
      }

      advanceList(data, resp => {
        if (resp.ret === 0) {
          this.list = resp.data.reservation_list || [];

          if (this.list.length !== 0) {
            this.isListEmpty = false;
          } else {
            this.isListEmpty = true;
          }

          this.list = this.list.map(item => {
            if (item.reservation_time) {
              item["reservation_time"] = moment(
                item.reservation_time * 1000
              ).format("YYYY-MM-DD HH:mm:ss");
            }

            if (item.sign_time) {
              item["sign_time"] = moment(item.sign_time * 1000).format(
                "YYYY-MM-DD HH:mm:ss"
              );
            }
            return item;
          });

          this.total = resp.data.total;
        } else {
          console.warn("获取列表错误！");
        }
      });
    },

    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("SLSTATISTICALLFORMSIZE", val, 30);
      this.getList();
    },
    //目标页
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    },

    sort(item) {
      this.sortLabel = item.prop;

      if (item.order === "descending") {
        this.isSort = true;
        this.sortOrder = -1;
      } else if (item.order === "ascending") {
        this.isSort = true;
        this.sortOrder = 1;
      } else {
        this.isSort = false;
      }

      this.getList();
    },

    checkData(startTime, endTime) {
      if (startTime === "" || endTime === "") {
        return true;
      }

      let start = parseInt(moment(startTime).format("X"));
      let end = parseInt(moment(endTime).format("X"));

      if (start > end) {
        this.$notify({
          title: "警告",
          message: "开始时间不能大于结束时间！",
          type: "warning"
        });
        return false;
      } else {
        return true;
      }
    },
    beginTimeChange() {
      let results = this.checkData(this.beginTime, this.endTime);
      if (!results) {
        this.beginTime = "";
      }
    },
    endTimeChange() {
      let results = this.checkData(this.beginTime, this.endTime);
      if (!results) {
        this.endTime = "";
      }
    },
    checkPhoneNum() {
      this.customerPhone = this.checkRound(this.customerPhone);
    },
    //check Round number 整数
    checkRound(value) {
      value = String(value);
      value = value.replace(/[^\d]/g, ""); //清除“数字”和“.”以外的字符
      if (value.indexOf(".") < 0 && value !== "") {
        value = parseFloat(value);
      }
      return value;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
#advance-table {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.search-content {
  @include fc(14px, #333);
  padding: 20px 14px;
  min-width: 1540px;
}

.pagination-container {
  text-align: center;
  padding: 50px 14px 20px;
}

.table-content {
  position: relative;
  padding-left: 14px;
  padding-right: 14px;
}
.search-item {
  margin-left: 20px;
  .search-label {
    height: 34px;
    line-height: 34px;
    margin-right: 14px;
    // margin-left: 14px;
  }
  .search-detail.time {
    margin-left: 14px;
  }
}
.search-item:first-child {
  margin-left: 0px;
  width: 565px;
  .search-label {
    margin-left: 0;
  }
}
.search-item.input-sec {
  margin-left: 10px;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#advance-table {
  .search-content {
    .input-text {
      input {
        @include wh(180px, 34px);
        border-radius: 0;
      }
    }
  }
  .el-pagination__jump {
    margin-left: 5px;
    margin-right: 15px;
  }
  .el-table td,
  .el-table th {
    height: 60px;
  }
  .el-table .cell,
  .el-table th > div {
    padding-left: 5px;
    padding-right: 5px;
  }
  .el-date-editor.el-input {
    width: 240px;

    input {
      border-radius: 0;
      height: 34px;
    }
  }
  .el-select {
    width: 180px;
    height: 34px;

    .el-input {
      font-size: 14px;

      input {
        border-radius: 0;
        height: 34px;
      }
    }
  }
  .el-button {
    @include wh(100px, 34px);
    border: 1px solid #4877e7;
    color: #4877e7;
    border-radius: 0;
    font-size: 14px;
  }
}
</style>


